
// ..
.Cities_item{
    margin-bottom: 1rem;
}


// 
.Cities_add-city {
    position: fixed;
    left: 0%;
    bottom: 50%;
    transform: translateX(-50%);

    height: 2.5rem;
    width: 2.5rem;

    border-radius: 50%;
    background-color: var(--md-bg-primary);
    box-shadow: 0 0 1px 2px 1px var(--md-bg-ccc);
    transition: transform 0.3s;
    &:hover {
        transform: translateX(0);    }
    &::after{
        content: '+';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -60%) scale(2);
    }
}


.Cities_input-report{
    width: 100%;
    padding: 0.5rem;
    overflow-y: scroll;
    background-color: transparent;
    resize: none;
}


// .
.Cities_skeleton{
    margin-top: 6.5rem;
}